* {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#theme {
  display: none;
}
#theme:checked ~ .app {
  filter: invert(1);
}

.app {
  width: 100%;
  min-height: 100vh;
  background: white;
}

.main {
  max-width: 1000px;
  width: 100%;
  margin: auto;
}

img {
  object-fit: cover;
}

/* -------- AUTH --------- */
@import url("./auth.css");

/* -------- LOADING --------- */
@import url("./loading.css");

/* -------- HEADER --------- */
@import url("./header.css");

/* -------- AVATAR --------- */
@import url("./avatar.css");

/* -------- PROFILE --------- */
@import url("./profile.css");

/* -------- Home --------- */
@import url("./home.css");

/* -------- Status Modal --------- */
@import url("./status_modal.css");

/* -------- Comments --------- */
@import url("./comments.css");

/* -------- Post Thumb --------- */
@import url("./post_thumb.css");

/* -------- Message --------- */
@import url("./message.css");

/* -------- Icons --------- */
@import url("./icons.css");

/* -------- Call Modal --------- */
@import url("./call_modal.css");

/* ------------ RESPONSIVE -------------- */
@media (max-width: 766px) {
  .header .navbar .logo {
    display: none;
  }

  .header .menu {
    width: 100%;
    height: 40px;
  }

  .header .menu .navbar-nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .header .search_form {
    width: 100%;
  }

  .header .logo {
    margin: auto;
  }

  .header .logo h1 {
    font-size: 1.5rem;
  }

  .message {
    height: calc(100vh - 100px);
  }
  .right_mess,
  .left_mess {
    display: none;
  }
  .status {
    padding: 20px 10px;
  }
}

.mode {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
